      </swiper>
    </div>
    <div class="hides hidesTop" ref="listTop">
      <div class="flex tabs text-center f28 nfixed">
        <span v-for="(item,index) in listTab" :class=" index===indexId ? 'active':''" :key="index" @click="toggleTab(index)">{{index === 0 ? item : item + '级'}}</span>
        <!-- <span v-for="(item,index) in listTab" :class=" index===indexId ? 'active':''" :key="index" @click="toggleTab(index)" v-if="index!=0">{{item}}级</span> -->
      </div>
    </div>
    <div class="type-list"
      v-infinite-scroll="loadMore"
      infinite-scroll-distance="200"
      :class="{mt110 : !activeTab , isComplete: isComplete}"
    >
      <div v-for="(item,index) in listData" :key="index">
        <div class="listDate flex" v-if="item.type !== 100"  :class="index===0?'dw1':''"  @click="toPage(item.level,item.id)">
          <div class="listImg">
            <img :src="item.thumb" alt="大图">
            <span class="tolevel f24">{{item.level}}级</span>
          </div>
          <div class="listCoten">
            <div class="f32 name carType fullname">{{item.fullname}}</div>
            <div class="f24 logo carName">
              <span :class="item.level >= 16 ? 'carLogo': 'shopLogo'">
                <img :src="item.brand_logo" alt="">
              </span>
            </div>
            <div class="shortname">{{item.shortname ? item.shortname : '暂无简称'}}</div>
            <div class="price flex"><span class="red f36 newPr">¥0.00</span><s class="gray f30 olderPr">¥ {{item.price|newMoney}}</s></div>
          </div>
        </div>
        <div class="advertise" v-else>
          <div class="rect"></div>
          <ad-view :adObj="item" @closeAd="deleteAd(index)"></ad-view>
          <div class="rect"></div>
        </div>
      </div>
      <div class="text-center" v-if="listData.length===0 && isComplete">暂无数据</div>
      <div class="loading" v-show="isLoading">加载中...</div>
      <div class="no-more-data" v-if="listData.length > 0 && isComplete">没有更多数据了！</div>
    </div>
  </div>
