</template>
<script>
import HeaderNavBar from '@/components/task/header-nav'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import adView from '@/components/mutiAdList'
export default {
  data () {
    let that = this
    return {
      setBrowser: {},
      clickAds: {},
      days: 0,
      hours: 0,
      minute: 0,
      second: 0,
      conten: [],
      listTab: [],
      indexId: 0,
      listData: [],
      activeTab: true,
      house: [],
      userData: [],
      user_icon: null,
      nickname: null,
      pageNo: 1,
      proCopyright: [],
      allLoaded: false, // 是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
      scrollMode: 'auto', // 移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
      totalpage: 0,
      isComplete: false,
      isLoading: false,
      swiperOptions: {
        autoHeight: true,
        slidesPerView: 1,
        spaceBetween: 30,
        // autoplay: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        loop: true,
        on: {
          transitionEnd () {
            let el = this.slides[this.activeIndex]
            let dataset = el.dataset
            if (that.house[dataset.idx].type === 100) {
              if (that.$utils.setObj(el, that.setBrowser)) {
                that.setBrowserNums(el.dataset.id)
                that.setBrowser[el.dataset.id] = true
              }
            }
          }
        },
        observer: true,
        observeParents: true,
        initialSlide: 0
      }
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    },
    share () {
      return this.$store.state.share
    }
  },
  beforeRouteLeave (to, from, next) {
    this.$utils.cache('awardPag', this.$utils.getScrollTop(this.$refs.awardPag))
    next()
  },
  activated () {
    if (this.$utils.cache('awardPag')) {
      this.$nextTick(() => {
        this.$refs.awardPag.scrollTop = this.$utils.cache('awardPag')
        this.$utils.cache('awardPag', 0)
      })
    }
  },
  mounted () {
    this.$refs.awardPag.scrollTop = 0
    this.isLoading = true
    this.loadPageList(0)  // 初次访问查询列表
    this.$refs.awardPag.addEventListener('scroll', this.handleScroll)
    this.user_icon = this.$utils.cache('user_icon')
    this.nickname = this.$utils.cache('nickname')
    this.$api.get('GOODS_GRAE', {}, res => {
      this.listTab = res.data
    })
    this.getUserInfo()
    this.getHous()
  },
  methods: {
    deleteAd (index) {
      this.listData.splice(index, 1)
    },
    toShopDetail () {
      if (this.userData.equivalence_current.tag === 'goods') {
        this.$router.push('/goods_detail/' + this.userData.equivalence_current.id)
      } else if (this.userData.equivalence_current.tag === 'car') {
        this.$router.push('/car_detail/' + this.userData.equivalence_current.id)
      }
    },
    toDetail (item) {
      this.$router.push({
        name: 'HouseDetail',
        params: {
          id: item.id
        }
      })
    },
    // 获取大礼包
    getGifts () {
      if (!this.commonState.login_bol) {
        this.$store.commit('common_login_box', true)
      } else {
        this.$store.commit('setShowShare', true)
        this.$store.commit('set_shareType', 9)
      }
    },
    // 获取用户信息
    getUserInfo () {
      this.$api.get('USER_INFO', {}, res => {
        this.userData = res.data
        if (!res.data.start_time) {
          this.days = '180天'
          this.hours = '0时'
          this.minute = '0分'
          this.second = '0秒'
        } else {
          this.isDev = true
          let expDate = new Date(res.data.start_time)
          expDate.setDate(expDate.getDate() + 180)
          if (this.time) {
            clearInterval(this.time)
            this.time = null
          }
          let resTime = this.$utils.timeDifference(new Date().getTime(), expDate)['string']
          this.days = resTime[0]
          this.hours = resTime[1]
          this.minute = resTime[2]
          this.second = resTime[3]
          this.time = setInterval(() => {
            let resTime = this.$utils.timeDifference(new Date().getTime(), expDate)['string']
            if (resTime.toString() === '0,0,0,0') {
              clearInterval(this.time)
            }
            this.days = resTime[0]
            this.hours = resTime[1]
            this.minute = resTime[2]
            this.second = resTime[3]
          }, 1000)
        }
      })
    },
    back () {
      this.$router.go(-1)
    },
    // 设置浏览器点击
    setBrowserClick (id) {
      let data = {
        ad_id: id,
        type: 1,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {})
    },
    /**
     * @method 设置广告浏览量
     * @param {string} id 广告id
     */
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {})
    },
    // 滚动函数
    scrollFn () {
      // 该节点下所有元素
      let ads = this.$refs.awardPag.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    handleScroll () {
      this.scrollFn()
      let scrollTop = this.$utils.getScrollTop(this.$refs.awardPag)
      let h = 0
      if (document.querySelector('.hidesTop')) {
        h = document.querySelector('.hidesTop').offsetTop
      }
      if (this.$utils.showNavBar()) {
        scrollTop += 48
      }
      if (scrollTop >= h) {
        this.activeTab = false
      } else  {
        this.activeTab = true
      }
    },
    toggleTab (index) {
      let rate = (375 / this.$utils.getDevideWidth()).toFixed(2)
      this.$refs.awardPag.scrollTop = this.$utils.getElposition(this.$refs.listTop) - 50 * rate
      this.indexId = index
      this.pageNo = 1
      this.isComplete = false
      this.isLoading = true
      setTimeout(() => {
        this.isLoading = false
      }, 10000)
      this.list = []
      this.loadPageList(index)
    },
    getHous () {
      this.$api.get('HOUSE_LIST', {ad_position: 17}, res => {
        this.house = res.data
        if (this.house.length > 0 && this.house[0].type === 'advertise') {
          this.setBrowser[this.house[0].id] = true
          this.setBrowserNums(this.house[0].id)
        }
      })
    },
    loadMore () {
      if (this.isComplete || this.isLoading) {
        setTimeout(() => {
          this.isLoading = false
        }, 1000)
        return
      }
      if (!this.isLoading) {
        this.isLoading = true
        setTimeout(() => {
          this.isLoading = false
        }, 1000)
        if (this.timer) {
          clearInterval(this.timer)
          this.timer = null
          return
        }
        this.timer = setTimeout(() => {
          this.more()// 上拉触发的分页查询
        }, 200)
      }
    },
    loadPageList (key) {
      this.$api.get('GOODS_LIST', {type: key, page: this.pageNo, ad_position: 18}, res => {
        this.isLoading = false
        this.listData = [...res.data.data]
        this.totalpage = res.data.last_page
        if (this.totalpage === 1) {
          this.allLoaded = true
          this.isComplete = true
        }
        this.$nextTick(function () {
          // 是否还有下一页，加个方法判断，没有下一页要禁止上拉
          this.scrollMode = 'touch'
        })
      })
    },
    more () {
      // 分页查询
      this.$api.get('GOODS_LIST', {type: this.indexId, page: this.pageNo}, res => {
        this.isLoading = false
        this.listData = [...this.listData, ...res.data.data]
        if (this.pageNo >= res.data.last_page) {
          this.isComplete = true
          this.allLoaded = true
        }
        this.pageNo++
      }, res => {
        this.isLoading = false
      })
    },
    toPage (item, id) {
      if (item > 15) {
        this.$router.push('/car_detail/' + id)
      } else {
        this.$router.push('/goods_detail/' + id)
      }
    },
    toPages (obj) {
      if (obj.tag && obj.id) {
        if (obj.tag === 'goods') {
          this.$router.push('/goods_detail/' + obj.id)
        } else if (obj.tag === 'car') {
          this.$router.push('/car_detail/' + obj.id)
        }
      }
    },
    // 跳转我的等级页面
    toMygradeinfoPage () {
      this.$router.push({name: 'Mygradeinfo'})
    },
    toHouser (index) {
      if (this.house[index].type === 100) {
        this.$utils.openAd(this.house[index].link)
        if (!this.clickAds[this.house[index].id]) {
          this.setBrowserClick(this.house[index].id)
          this.clickAds[this.hours[index].id] = true
        }
      } else {
        this.$router.push('/house_detail/' + this.house[index].id)
      }
    }
  },
  watch: {
    'share.showSuccessShare' (flag) {
      if (!flag && this.share.shareType === 10 &&  this.commonState.userInfo.achieved_gift_bag === 1) {
        this.getUserInfo()
      }
    },
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.getUserInfo()
      }
    },
    $route (r) {
      if (r.path === '/prize' && this.commonState.login_bol) {
        this.getUserInfo()
      } else if (r.path === '/pirze' && !this.commonState.login_bol) {
        this.userData = {}
      }
    }
  },
  components: {
    HeaderNavBar,
    swiper,
    swiperSlide,
    adView
  },
  filters: {
    newMoney (num) {
      num = num || 0
      if (num > 9999) {
        let long = length || 2
        return (num / 10000).toFixed(long) + '万'
      } else {
        return num
      }
    }
  },
  destroyed () { // 离开该页面需要移除这个监听的事件
    this.$refs.awardPag.removeEventListener('scroll', this.handleScroll)
  }
}
</script>
