      color: #232323;
      .title {
        font-size: 32px;
        margin: 25px 0 12px;
        line-height: 38px;
        font-weight: 900;
      }
      .iconImage {
        width: 60px;
        height: 60px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        margin-right: 6px;
        overflow: hidden;
      }
      .shopLogo {
        width: 180px;
        margin-right: 16px;
      }
      .content {
        height: 60px;
        font-size: 30px;
        margin-top: 30px!important;
        // justify-content: space-between!important;
        align-items: flex-end;
        &.isShop {
          margin-top: 12px;
          margin-bottom: 10px;
        }
        .left-content {
          align-items: flex-end;
        }
      }
      .price {
        font-size: 36px;
        color: #e71010;
        margin-top: 16px;
        font-weight: 600;
        display: flex;
        align-items: flex-end;
        span {
          display: inline-block;
        }
        .olderPr {
          display: flex;
          align-items: flex-end
        }
      }
      .shortname {
        font-size: 32px;
        padding-top: 4px;
        margin-right: 64px;
      }
      .shopname {
        font-size: 30px;
      }
    }
    .goods{
      position: relative;
      .listImg{
        height: 138px;
        width: 250px;
        margin-left: 26px;
        .listImg-IMG{
          height: inherit;
        }
      }
      .listIMG-title{
        background: #F37F32;
        color: white;
        width: 250px;
        height: 40px;
        text-align: center;
        line-height: 40px;
      }
      .listCoten{
        height: 178px;
        width: 380px;
        overflow: hidden;
        box-sizing: border-box;
        margin-left: 25px;
        .carType{
          box-sizing: border-box;
          overflow: hidden;
        }
        .carName{
          // margin: 20px 0 25px 0;
          span{
            display: inline-block;
            line-height: 60px;
            height: 60px;
            &:first-child{
              width: 60px;
              margin-right: 10px;
              img{
                width: 100%;
              }
            }
          }
        }
      }
    }
  }
}
.bigGifts {
  padding: 30px 0 40px;
  background: #f2f2f2;
  .big_bg {
    width: 694px;
    height: 384px;
    margin: 0 auto;
    position: relative;
    background-image: url('https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/all_prize_car_bg.png');
    // 用户信息
    .userInfo {
      color: #fff;
      padding: 30px;
      font-size: 30px;
      .avatar{
        width: 72px;
        height: 72px;
        overflow: hidden;
        border-radius: 50%;
        margin-right: 10px;
      }
      .grade {
        padding: 0 10px;
        height: 30px;
        background: #dfa378;
        border-radius: 8px;
        margin-left: 10px;
        font-size: 24px;
        .iconSize {
          width: 23px;
          height: 18px;
        }
      }

    }
    // 立即领取按钮
    .button {
      background-image: url('../../assets/img/my/my_bag_big_button.png');
      width: 285px;
      height: 63px;
      color: #ee3847;
      font-size: 36px;
      bottom: 30px;
    }
  }
  .countDown {
    background: #f2e2d6;
    border-radius: 45px;
    width: 690px;
    margin: 20px auto 0;
    height: 90px;
    border-radius: 45px;
    font-size: 36px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    color: #FF7200;
  }
}
.gifts{
  .big_bg{
    background-image: url('https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/all_prize_bag_bg.png');
  }
  .price {
    bottom: 60px;
    font-size: 36px;
    color: rgb(244, 226, 0)
  }
}
.count_down_box {
  background: #f2f2f2;
  margin: 43px 0 60px 0;
  .count_down{
    width: 690px;
    height: 90px;
    line-height: 90px;
    background: #AEAEAE;
    border-radius: 45px;
    margin: 0 auto;
    // margin: auto;
    // margin-top: 43px;
    // margin-bottom: 100px;
    color: white;
  }
}

.tabs{
  width: 100%;
  padding: 25px 35px;
  /*height: 60px;*/
  box-sizing: border-box;
  font-size: 36px;
  color: #232323;
  line-height: 60px;
  span{
    flex: 1;
    &.active{
      background: #F37F32;
      border-radius: 30px;
      color: white;
    }
  }
}
.hides{
  width: 100%;
  height: 110px;
  background: white;
  box-sizing: border-box;
  border-bottom: 1px solid #F5F5F5;
  &.fixed {
    top: 88px;
    background: #fff;
    z-index: 2;
    &.isBrowser {
      top: 0;
    }
  }
}
  .red{
    color: #E71010;
  }
  .gray{
    color: #aeaeae;
  }
  .advertise {
    padding: 0 30px;
    .rect {
      height: 10px;
      width: 750px;
      margin-left: -30px;
      background: #f5f5f5;
    }
  }
  .type-list{
    &.isComplete {
      padding-bottom: 0px;
    }
    background: white;
    padding-bottom: 40px;
    .listDate{
      padding: 28px 30px;
      box-sizing: border-box;
      border-bottom: 1px solid #F5F5F5;
      justify-content: space-between;
      .price{
        justify-content: flex-start;
        line-height: 44px;
        margin-top: 16px;
        .red {
          font-size: 36px;
          font-weight: 600;
          margin-right: 20px;
        }
        .olderPr {
          font-size: 24px;
        }
      }
    }
    .listImg{
      width: 300px;
      height: 227px;
      margin-right: 23px;
      position: relative;
      .tolevel{
        position: absolute;
        top: 0;
        left: 0;
        color: white;
        background: url('../../assets/img/my/grade.png') no-repeat center;
        width: 70px;
        height: 28px;
        line-height: 29px;
        box-sizing: border-box;
        padding-left: 2px;
        background-size: 100% 100%;
      }
    }
    .listCoten{
      flex: 1;
      min-width: 0;
      /*margin-top: 7px;*/
      .carType{
        line-height: 45px;
        max-height: 80px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        font-weight: 900
      }
      .shortname {
        font-size: 28px;
        margin-top: 10px;
        line-height: 40px;
      }
      .carName{
        font-size: 28px;
        line-height: 34px;
        vertical-align: middle;
        > * {
          vertical-align: middle;
        }
      }
      .carName>span{
        width: 60px;
        height: 60px;
        line-height: 60px;
        margin-right: 4px;
        margin-left: -10px;
        display: inline-block;
        &.carLogo {
          height: 60px;
        }
        &.shopLogo {
          width: 180px;
          height: 60px;
          margin-right: 0
        }
        &.nowrap{
          width: auto;
        }
      }
      
    }
  }
  .nfixed{
    position: relative;
  }

  .fixeds{
    position: fixed;
    top: 0;
    background: white;
    // padding: 10px 0;
    width: 100%;
    z-index: 100;
    /*box-shadow: 2px 2px 8px 2px #E0E0E0;*/
    /*transition: 0.2s linear;*/
  }
  img{
    width: 100%;
    height: 100%;
  }
  .Carousel{
    width: 750px;
    // height: 882px;
    overflow:hidden;
    background: white;
    margin-bottom: 30px;
    .title{
      font-size: 36px;
      color: #F37F32;
      padding: 25px 0 20px;
    }
    .swiper-houser {
      padding-bottom: 34px!important;
      position: relative;
    }
  }
  .sw-list{
    .sw-img{
      width: 750px;
      height: 500px;
    }
    .sw-text{
      height: 237px;
      position: relative;
      padding: 20px 30px 0;
    }
    .sw-pri{
      line-height: 58px;
    }
    .sw-title{
      line-height: 45px;
      margin-bottom: 6px;
      font-size: 32px;
      font-weight: 900;
    }
    .sw-loc{
      line-height: 42px;
      font-size: 30px;
      color: #909090;
      margin-bottom: 12px;
    }
    .sw-type{
      margin-bottom: 20px;
      height: 42px;
      box-sizing: border-box;
      font-size: 24px;
      .square {
        height: 34px;
        color: #909090;
        border: 1px solid #909090;
        margin-right: 10px;
        line-height: 34px;
        padding: 0 8px;
      }
      .square0 {
        border-color: #E71010;
        color: #e71010;
      }
    }
    .sw-pri{
      line-height: 58px;
      span{
        margin-right: 20px;
        color: #E71010;
        font-weight: 600;
        font-size: 48px;
      }
      s{
        color: #909090;
      }
      .button {
        border: 1px solid #FF7F18;
        width: 178px;
        height: 60px;
        margin-right: 45px;
        border-radius: 30px;
        color: #FF7F18;
        line-height: 62px;
      }
    }
    .sw-trans{
      width: 220px;
      height: 90px;
      line-height: 90px;
      box-sizing: border-box;
      border: 2px solid #3F8BDE;
      border-radius: 8px;
      color:#3F8BDE ;
      text-align: center;
      position: absolute;
      bottom: 0px;
      right: 0;
    }
  }
  #awardPag.hasHeader .fixeds {
    top: 88px!important;
  }
</style>



